.carousel-section {
	margin-bottom: 30px;
}

.carousel {

}

.carousel-item {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	width: 100%;
	height: 330px;
	background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0.001) 100%),
		url("./../img/carousel/slide-01.jpg");
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	border-radius: 6px;
	z-index: 1;
}

